import React from "react";
import { useForm } from "react-hook-form";
import Input from "../input/input";
import Title from "../Title/Title";
import Button from "../Title/Button";
import { useAuth } from "../../hooks/useAuth";

export default function ChangePassword() {
    const {
        handleSubmit,
        register,
        getValues,
        formState: { errors },
    } = useForm();

    const { changePassword } = useAuth();
    const submit = (passwords) => {
        changePassword(passwords);
    };

    return (
        <div>
            <form onSubmit={handleSubmit(submit)}>
            <Title title="Cambiar Contrasea" />
                <Input
                    type="password"
                    label="Contrasea Actual"
                    {...register("currentPassword", {
                        required: true,
                    })}
                    error={errors.currentPassword}
                />

                <Input
                    type="password"
                    label="Nueva Contrasea"
                    {...register("newPassword", {
                        required: true,
                        minLength: 5,
                    })}
                    error={errors.newPassword}
                />

                <Input
                    type="password"
                    label="Confirmar Nueva Contrasea"
                    {...register("confirmNewPassword", {
                        required: true,
                        validate: (value) =>
                            value !== getValues("newPassword")
                                ? "Las contraseas no coinciden"
                                : true,
                    })}
                    error={errors.confirmNewPassword}
                />

                <Button type="submit" text="Cambiar" />
            </form>
        </div>
    );
}
